.markdownShower {
  text-align: left;
  vertical-align: top;
  box-sizing: border-box;
  width: 100%;
  display: inline-block;

  & blockquote {
    border-left: 2px solid #ccc;
    margin: 0;
    padding: 2px 0 2px 10px;
    font-size: 1.05em;
  }

  & h1, & h2, & h3, & h4, & h5, & h6 {
    color: var(--color);
    margin-block-end: .3em;
    padding-bottom: .3em;
  }

  & ul, & ol {
    padding-inline-start: 1em;

    & li {
      padding: .3rem 0;
    }
  }

  & ul {
    list-style-type: circle;
  }

  & p {
    white-space: pre-wrap;
    margin-block: .3rem;
  }

  & :global(.keyword) {
    color: #908080;
    box-sizing: border-box;
    background: #ff0;
    border-radius: 4px;
    margin: 0 3px;
    padding: 0 3px;
    font-size: 14px;
  }

  & :global(code.hljs) {
    margin: 0;
  }
}

code {
  color: #dcdcdc;
  background: #797979;
  border-radius: 6px;
  margin: 0 4px;
  padding: 3px 8px;
}

